<script lang="ts">
    import AcModal from "ACUi/AcModal.svelte";

    export let changelogUrl;
    export let closeHandler: Function = null;
    export let show = true;

    let loading = true;

    export const toggle = (toggle: boolean = true) => {
        show = toggle;
    }

    const close = () => {
        show = false;
        if (closeHandler) {
            closeHandler();
        }
    }
</script>

<AcModal contentNoPadding className="changelog" bind:visible={show} on:close={close}>
	<div slot="content" class="-content">
		{#if loading }
			<span class="spinner" style="visibility: visible"></span>
		{/if}
		<iframe src="{changelogUrl}" width="100%" title="changelog" on:load={() => loading=false}></iframe>
	</div>
</AcModal>
