import AcHtmlElement from "AC/helpers/html-element";
import AcConfirmation from "AC/plugin/ac-confirmation";
import Updates from "./license-manager/updates";
import { getLicenceManagerTranslations } from "./license-manager/types";
const i18n = getLicenceManagerTranslations();
const initForm = (input, button) => {
    button.disabled = input.value === '';
};
document.addEventListener('DOMContentLoaded', () => {
    const input = document.querySelector('#licence_activation input[name=license]');
    const button = document.querySelector('#licence_activation button');
    if (input && button) {
        initForm(input, button);
        new AcHtmlElement(input).addEventListeners(['keyup', 'input'], () => initForm(input, button));
    }
    const removeButton = document.querySelector('#licence_activation button[value="acp-license-deactivate"]');
    if (removeButton) {
        removeButton.addEventListener('click', function (e) {
            e.preventDefault();
            const message = i18n.license_removal + ' ' + i18n.license_removal_explanation;
            new AcConfirmation({
                message: message,
                translation: i18n.confirmation,
                confirm: () => {
                    const form = removeButton.closest('form');
                    if (form) {
                        form.appendChild(AcHtmlElement.create('input').setAttributes({ type: 'hidden', name: 'action', value: 'acp-license-deactivate' }).getElement());
                        form.submit();
                    }
                }
            }).create();
        });
    }
    document.querySelectorAll('.ac-settings-box.-updates').forEach(el => new Updates(el));
});
